* {
    padding: 0;
    margin: 0;
    box-sizing: border-box; }

.container {
    display: flex;
    background-size: auto 100%;
    width: 100vw;
    height: 100vh;
    background: url("/public/img/Hiking.jpg") no-repeat fixed center center; }

.column {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%; }

.wrapper {
    display: flex; }

.align-center {
    display: flex;
    align-items: center;
    align-content: space-between; }

.justify-content {
    display: flex; }

.row {
    display: flex;
    flex-direction: row; }

.logo-wrapper {
    height: 30%; }

.form-wrapper {
    //width: 900px
    height: 47px;
    background: #FFFFFF;
    border-radius: 50px; }
.form__form-item {
    width: 274px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: inset -1px 0 0 #E2E2E1;
    label {
        display: flex;
        padding-left: 15px; } }

.form__form-item:first-child {
    padding-left: 5px; }
.form__form-item:nth-last-of-type(-n+2) {
    width: 186px;
    box-shadow: none; }

.form__form-item:last-child {
    width: 150px;
    box-shadow: none; }

.form-wrapper-item {
    font-size: 16px;
    padding: 6px 0 6px 10px;
    border: none;
    align-items: center; }


.form-item-menu {
    display: flex;
    width: 100%;
    position: relative;
    padding: 6px 0 6px 10px;
    list-style: none;
    font-family: Montserrat;
    font-style: normal;
    line-height: 20px;
    letter-spacing: -0.02em;
    color: #767676;
    font-weight: 600; }
.form-item-menu-arrows {
    position: relative;
    left: 5em; }
.form-item-menu:active .form-item-menu-dropdown {
    display: block; }
.form-item-menu-dropdown {
    position: absolute;
    display: none;
    list-style: none; }


.form-item-button {
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    width: 150px;
    height: 50px;
    border: 0 solid #F9C731;
    background: #F9C731;
    font-family: Montserrat;
    font-weight: bold;
    font-size: 14px;
    letter-spacing: -2%;
    color: #024590;
    text-transform: uppercase; }

input::placeholder {
    font-family: Montserrat;
    font-style: normal;
    line-height: 20px;
    letter-spacing: -0.02em;
    align-items: center;
    color: black;
    font-weight: 600; }

.form-wrapper__form-item-dropdown {
    display: none; }
.title {
    margin-top: 65px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 55px;
    line-height: 67px;
    text-align: center;
    letter-spacing: -0.05em;
    color: #FFFFFF; }

